import React from "react";
import styles from "./Whatsapp.module.css";
import { Col, Row } from "react-bootstrap";
import whatsapp from "../../assets/whaysapp.png";
import { Tooltip } from "antd";

const Whatsapp = () => {
  const text = <span>Whatsapp Us</span>;
  return (
    <section>
      <Row>
        <Col lg={12} md={12} sm={12}>
          <div id="whatsapp-widget">
            <a href="https://wa.me/message/UYSCPWRCUFFAI1" target="_blank">
              <Tooltip placement="topLeft" title={text}>
                <img
                  src={whatsapp}
                  width={53}
                  className={styles["Whats_app_backGround"]}
                />
              </Tooltip>
            </a>
          </div>
        </Col>
      </Row>
    </section>
  );
};

export default Whatsapp;
