import React from "react";
import styles from "./ContactUs.module.css";
import { Button, Col, Container, Row } from "react-bootstrap";
import { MDBCarousel, MDBCarouselItem } from "mdb-react-ui-kit";
import Contactus from "../../assets/best-contact-us-pages-2.png";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import AccountCircle from "@material-ui/icons/AccountCircle";
import mail from "../../assets/mail3.png";
import Phone from "../../assets/phone2.png";
import Messege from "../../assets/messege.png";

const ContactUs = () => {
  const useStyles = makeStyles((theme) => ({
    margin: {
      margin: theme.spacing(1),
    },
  }));

  const classes = useStyles();

  return (
    <>
      <section>
        <Row className="mt-4">
          <Col lg={12} md={12} sm={12}>
            <MDBCarousel showIndicators showControls fade>
              <MDBCarouselItem
                className={styles["ImagesCrousel"]}
                itemId={1}
                src="https://mdbootstrap.com/img/Photos/Slides/img%20(15).jpg"
                alt="..."
              >
                <h5>First slide label</h5>
                <p>
                  Nulla vitae elit libero, a pharetra augue mollis interdum.
                </p>
              </MDBCarouselItem>

              <MDBCarouselItem
                className={styles["ImagesCrousel"]}
                itemId={2}
                src="https://mdbootstrap.com/img/Photos/Slides/img%20(22).jpg"
                alt="..."
              >
                <h5>Second slide label</h5>
                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
              </MDBCarouselItem>

              <MDBCarouselItem
                className={styles["ImagesCrousel"]}
                itemId={3}
                src="https://mdbootstrap.com/img/Photos/Slides/img%20(23).jpg"
                alt="..."
              >
                <h5>Third slide label</h5>
                <p>
                  Praesent commodo cursus magna, vel scelerisque nisl
                  consectetur.
                </p>
              </MDBCarouselItem>
            </MDBCarousel>
          </Col>
        </Row>
        <Row className="mt-3">
          <Col
            lg={12}
            md={12}
            sm={12}
            className="d-flex justify-content-center"
          >
            <span className={styles["slogan"]}>
              Talk to Us, We're Listening
            </span>
          </Col>
        </Row>
        <Row className="mt-3">
          <Col lg={1} md={1} sm={1}></Col>
          <Col lg={5} md={5} sm={5} className="ps-5">
            <Row className="mt-4">
              <Col lg={12} md={12} sm={12}>
                <section className={styles["InfoCardSection"]}>
                  <Row>
                    <Col lg={6} md={6} sm={6}>
                      <Row>
                        <Col lg={12} md={12} sm={12}>
                          <div className={classes.margin}>
                            <Grid container spacing={1} alignItems="flex-end">
                              <Grid item>
                                <AccountCircle />
                              </Grid>
                              <Grid item>
                                <TextField
                                  id="input-with-icon-grid"
                                  label="Name"
                                />
                              </Grid>
                            </Grid>
                          </div>
                        </Col>
                      </Row>
                      <Row className="mt-2">
                        <Col lg={12} md={12} sm={12}>
                          <div className={classes.margin}>
                            <Grid container spacing={1} alignItems="flex-end">
                              <Grid item>
                                <img src={mail} height="22px" width="22px" />
                              </Grid>
                              <Grid item>
                                <TextField
                                  id="input-with-icon-grid"
                                  label="Email"
                                />
                              </Grid>
                            </Grid>
                          </div>
                        </Col>
                      </Row>
                    </Col>
                    <Col lg={6} md={6} sm={6}>
                      <Row className="mt-2">
                        <Col lg={12} md={12} sm={12}>
                          <div className={classes.margin}>
                            <Grid container spacing={1} alignItems="flex-end">
                              <Grid item>
                                <img src={Phone} height="22px" width="22px" />
                              </Grid>
                              <Grid item>
                                <TextField
                                  id="input-with-icon-grid"
                                  label="Contact No"
                                />
                              </Grid>
                            </Grid>
                          </div>
                        </Col>
                      </Row>
                      <Row>
                        <Col lg={12} md={12} sm={12}>
                          <div className={classes.margin}>
                            <Grid container spacing={1} alignItems="flex-end">
                              <Grid item>
                                <img src={Messege} height="22px" width="22px" />
                              </Grid>
                              <Grid item>
                                <TextField
                                  id="input-with-icon-grid"
                                  label="Your Messege"
                                />
                              </Grid>
                            </Grid>
                          </div>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="mt-4 m-0 p-0">
                    <Col
                      lg={12}
                      md={12}
                      sm={12}
                      className="d-flex justify-content-end "
                    >
                      <Button>Send</Button>
                    </Col>
                  </Row>
                </section>
              </Col>
            </Row>
          </Col>
          <Col lg={5} md={5} sm={5} className="ps-5">
            <img src={Contactus} width={600} />
          </Col>
          <Col lg={1} md={1} sm={1}></Col>
        </Row>
      </section>
    </>
  );
};

export default ContactUs;
