import React, { useState } from "react";
import styles from "./WebDevelopment.module.css";
import { Col, Container, Row } from "react-bootstrap";
import webdesign from "../../assets/webd.jpg";
import backButton from "../../assets/arrow-left-circle-fill.svg";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion";
const WebDevelopment = ({
  setwebData,
  setdigitalMarketing,
  setgraphicsDesign,
}) => {
  const navigate = useNavigate();
  const [subCategories, setSubCategories] = useState([
    {
      name: "Front-End Development",
    },
    {
      name: "Back-End Development",
    },
    {
      name: "Full-Stack Development",
    },
    {
      name: "Web Design",
    },
  ]);

  const HandlebackButton = () => {
    setwebData(false);
    setdigitalMarketing(true);
    setgraphicsDesign(true);
  };

  const animationVariants = {
    initial: { opacity: 0, y: 100 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -100 },
  };

  const cardVariants = {
    hover: {
      scale: 1.05, // Increase scale on hover to make it prominent
    },
    initial: {
      scale: 1, // Original scale
      boxShadow: "0px 2px 5px rgba(0, 0, 0, 0.1)", // Original shadow
    },
  };

  return (
    <Container>
      <Row>
        <Col lg={12} md={12} sm={12}>
          <span onClick={HandlebackButton}>
            <img src={backButton} width={33} className="cursor-pointer" />
          </span>
        </Col>
      </Row>
      <Row className="mt-5">
        <Col lg={12} md={12} sm={12} className="d-flex justify-content-center">
          <motion.div
            initial="initial"
            animate="animate"
            exit="exit"
            variants={animationVariants}
            transition={{ duration: 0.5 }}
          >
            <h1>WebDevelopment</h1>
          </motion.div>
        </Col>
      </Row>

      <motion.div
        initial="initial"
        animate="animate"
        exit="exit"
        variants={animationVariants}
        transition={{ duration: 0.5 }}
      >
        <Row className="mt-4">
          {subCategories.length > 0
            ? subCategories.map((data, index) => {
                return (
                  <>
                    <Col lg={3} md={3} sm={3}>
                      <motion.div
                        whileHover="hover" // Apply the hover animation on hover
                        variants={cardVariants}
                      >
                        <Row>
                          <Col
                            lg={12}
                            md={12}
                            sm={12}
                            className={styles["OverAll_padding"]}
                          >
                            <Row>
                              <Col lg={12} md={12} sm={12}>
                                <section className={styles["SubCategoryBoxes"]}>
                                  <img
                                    src={webdesign}
                                    className={styles["webDevelopmentImage"]}
                                  />
                                </section>
                              </Col>
                            </Row>
                            <Row className="mt-2">
                              <Col lg={12} md={12} sm={12}>
                                <span className={styles["Name_card_Styles"]}>
                                  {data.name}
                                </span>
                              </Col>
                            </Row>
                            <Row className="mt-1">
                              <Col lg={12} md={12} sm={12}>
                                <span
                                  className={styles["SubHeading_Cards_Price"]}
                                >
                                  $16.99
                                </span>
                              </Col>
                            </Row>
                          </Col>
                        </Row>
                      </motion.div>
                    </Col>
                  </>
                );
              })
            : null}
        </Row>
      </motion.div>
    </Container>
  );
};

export default WebDevelopment;
