import React from "react";
import styles from "./Carrers.module.css";
import { Col, Container, Row } from "react-bootstrap";
import backGroundImage from "../../assets/pexels-ivan-samkov-7213192.jpg";
import Typed from "react-typed";
import Select from "react-select";

const Carrers = () => {
  const options = [
    { value: "chocolate", label: "Chocolate" },
    { value: "strawberry", label: "Strawberry" },
    { value: "vanilla", label: "Vanilla" },
  ];
  return (
    <section className={styles["HOrizontal_Width"]}>
      <Row className="mt-3">
        <Col lg={12} md={12} sm={12}>
          <section>
            <div className={styles["Wallpaper"]}>
              <img
                src={backGroundImage}
                alt="Background"
                className={styles["background-image"]}
              />

              <div className={styles["overlay"]}>
                <Row>
                  <Col lg={12} md={12} sm={12} className="mt-5">
                    <span className={styles["First_Heading"]}>
                      <Typed
                        strings={["Careers"]}
                        loop={true}
                        typeSpeed={50}
                        showCursor={true}
                        cursorChar="|"
                      />
                    </span>
                  </Col>
                </Row>
              </div>
            </div>
          </section>
        </Col>
      </Row>
      <Row>
        <Col lg={12} md={12} sm={12}>
          <span className={styles["Heading"]}>
            Search Software Development Career Openings
          </span>
        </Col>
      </Row>
      <Row>
        <Col lg={12} md={12} sm={12} className={styles["bleowParagraph"]}>
          <span>
            Are you ready to immerse yourself in a world of talent, grow your
            skills, and be part of the solution to some of the world's most
            complex challenges? Discover your next software development career
            at Codinets.
          </span>
        </Col>
      </Row>
      <Row className={styles["SELECT"]}>
        <Col lg={3} md={3} sm={3}>
          <Select
            defaultValue={[options[2], options[3]]}
            isMulti
            name="colors"
            options={options}
            className="basic-multi-select"
            classNamePrefix="select"
          />
        </Col>
        <Col lg={3} md={3} sm={3}>
          <Select
            defaultValue={[options[2], options[3]]}
            isMulti
            name="colors"
            options={options}
            className="basic-multi-select"
            classNamePrefix="select"
          />
        </Col>
        <Col lg={3} md={3} sm={3}>
          <Select
            defaultValue={[options[2], options[3]]}
            isMulti
            name="colors"
            options={options}
            className="basic-multi-select"
            classNamePrefix="select"
          />
        </Col>
        <Col lg={3} md={3} sm={3}>
          <Select
            defaultValue={[options[2], options[3]]}
            isMulti
            name="colors"
            options={options}
            className="basic-multi-select"
            classNamePrefix="select"
          />
        </Col>
      </Row>
    </section>
  );
};

export default Carrers;
