import React, { useState } from "react";
import styles from "./Outlet.module.css";
import backGroundImage from "../../assets/pexels-ivan-samkov-7213192.jpg";
import { Col, Row } from "react-bootstrap";
import webDevelopment from "../../assets/developmentone.jpg";
import graphics from "../../assets/graphics.jpg";
import Digital from "../../assets/digitalmarketing.jpg";
import { useNavigate } from "react-router-dom";
import Typed from "react-typed";
import { motion } from "framer-motion";
import WebDevelopment from "../WebDevelopment/WebDevelopment";
import profile from "../../assets/profile.png";
import DigitalMarketing from "../DigitalMarketing/DigitalMarketing";
import GraphicsDesign from "../GraphicsDesignScreen/GraphicsDesign";
import buildingPng from "../../assets/building.png";

const Outlet = () => {
  const navigate = useNavigate();
  const [webDevDepart, setwebDevDepart] = useState(true);
  const [digitalMarketing, setdigitalMarketing] = useState(true);
  const [graphicsDesign, setgraphicsDesign] = useState(true);
  const [webData, setwebData] = useState(false);
  const [digitalmarketdata, setdigitalmarketdata] = useState(false);
  const [graphicsData, setgraphicsData] = useState(false);

  const navigateToWebDevelopment = () => {
    setwebDevDepart(true);
    setwebData(true);
    setdigitalMarketing(false);
    setgraphicsDesign(false);
    setdigitalmarketdata(false);
    setgraphicsData(false);
  };

  const handleDigitalMarketing = () => {
    setdigitalMarketing(true);
    setdigitalmarketdata(true);
    setwebDevDepart(false);
    setgraphicsDesign(false);
    setwebData(false);
    setgraphicsData(false);
  };

  const handleGraphicsDesign = () => {
    setgraphicsDesign(true);
    setgraphicsData(true);
    setdigitalMarketing(false);
    setwebDevDepart(false);
    setwebData(false);
    setdigitalmarketdata(false);
  };

  const cardVariants = {
    hover: {
      scale: 1.05, // Increase scale on hover to make it prominent
    },
    initial: {
      scale: 1, // Original scale
      boxShadow: "0px 2px 5px rgba(0, 0, 0, 0.1)", // Original shadow
    },
  };

  return (
    <Row>
      <Col lg={12} md={12} sm={12}>
        <section className={styles["HorizontalScroller"]}>
          <div className={styles["Wallpaper"]}>
            <img
              src={backGroundImage}
              alt="Background"
              className={styles["background-image"]}
            />

            <div className={styles["overlay"]}>
              <Row>
                <Col lg={12} md={12} sm={12} className="mt-5">
                  <span className={styles["First_Heading"]}>
                    <Typed
                      strings={["Your Partner, For Innovation"]}
                      // fadeOut={true}
                      // fadeOutClass="typed-fade-out"
                      // fadeOutDelay={500}
                      // backSpeed={0}
                      loop={true}
                      typeSpeed={50}
                      showCursor={true}
                      cursorChar="|"
                    />
                  </span>
                </Col>
              </Row>
            </div>
          </div>

          <Row className="mt-3">
            <Col lg={1} md={1} sm={1}></Col>
            <Col
              lg={11}
              md={11}
              sm={11}
              className="d-flex justify-content-center"
            >
              <span className={styles["More_headings"]}>
                Unlocking Your Digital Potential: Your Success, Our Mission
              </span>
            </Col>
          </Row>
          <Row>
            <Col lg={1} md={1} sm={1}></Col>
            <Col lg={11} md={11} sm={11} className="m-0 p-0">
              <section className={styles["Bio_Data"]}>
                <p>Codinets - Excellence in IT Consulting</p>
                <p>
                  The Exceptional businesses we buy represent a lifetime of
                  effort and talent, and they share three traits that we believe
                  make them Exceptional:
                </p>
                <Row className={styles["Bullet_points_Style"]}>
                  <Col lg={4} md={4} sm={4}>
                    <span>Quality Production</span>
                  </Col>
                  <Col lg={4} md={4} sm={4}>
                    <span>Punctuality</span>
                  </Col>
                  <Col lg={4} md={4} sm={4}>
                    <span>Assurity</span>
                  </Col>
                </Row>
                <p>
                  At Codinets, we're more than just IT consultants; we're your
                  strategic technology partners. Specializing in ASP.NET Core
                  backend and React.js frontend development, we empower
                  businesses with cutting-edge, custom solutions. Our team
                  combines technical expertise with creative innovation to drive
                  your digital presence forward.
                </p>
                <p>
                  Codinets is driven by a relentless pursuit of excellence. We
                  understand that technology is the backbone of modern
                  businesses, and our mission is to provide solutions that not
                  only meet your current needs but also position you for future
                  success. Whether it's optimizing your backend infrastructure
                  with ASP.NET Core or creating dynamic user interfaces with
                  React.js, we pride ourselves on delivering results that go
                  beyond your expectations. With Codinets as your partner, you
                  can be confident that your IT initiatives will be executed
                  with precision and innovation, making your business stand out
                  in the digital landscape.
                </p>
                <p>
                  In an increasingly digital world, your success hinges on
                  innovative technology solutions. Codinets is here to guide you
                  through the intricacies of IT, ensuring you stay competitive
                  and ahead of the curve. Let's embark on this journey together
                  and unlock your business's full potential.
                </p>
                <p>
                  Comprehensive Solutions: From concept to deployment, we offer
                  end-to-end IT services tailored to your unique needs.
                  Industry-Leading Technologies: Our proficiency in ASP.NET Core
                  and React.js ensures your applications are built with the
                  latest technology stack. Client-Centric Approach: We
                  prioritize your goals, ensuring every project aligns with your
                  business objectives. Proven Track Record: With a history of
                  successful projects, we've earned a reputation for quality and
                  reliability.
                </p>
              </section>
            </Col>
          </Row>

          <Row className="mt-5">
            <Col lg={12} md={12} sm={12}>
              <section className={styles["BackGroundOurPhilosophy"]}>
                <Row className="mt-3">
                  <Col
                    lg={12}
                    md={12}
                    sm={12}
                    className="d-flex justify-content-center"
                  >
                    <span className={styles["ourPhilosophy"]}>WHO WE ARE</span>
                  </Col>
                </Row>
                <Row>
                  <Col lg={12} md={12} sm={12}>
                    <section className={styles["PhilosophyClass"]}>
                      <span>
                        "At Codinets, we're more than just an IT consulting
                        firm. We're the architects of innovation, dedicated to
                        propelling the digital world into the future. With a
                        passion for cutting-edge technology and a commitment to
                        excellence, we empower businesses to thrive in the
                        ever-evolving digital landscape. Our team of experts is
                        your strategic partner in unlocking new possibilities
                        and driving sustainable growth. Welcome to the future of
                        IT with Codinets."
                      </span>
                    </section>
                  </Col>
                </Row>
              </section>
            </Col>
          </Row>
          <Row className="mt-4">
            <Col
              lg={12}
              md={12}
              sm={12}
              className="d-flex justify-content-center"
            >
              <span className={styles["More_headings_Services"]}>
                Our Services
              </span>
            </Col>
          </Row>
          <Row>
            <Col lg={12} md={12} sm={12} className={styles["OverAll_Padding"]}>
              <Row className="d-flex justify-content-center gap-5">
                {webDevDepart ? (
                  <>
                    <Col
                      lg={4}
                      md={4}
                      sm={4}
                      className={styles["Box_For_Options"]}
                    >
                      <motion.div
                        whileHover="hover" // Apply the hover animation on hover
                        variants={cardVariants}
                      >
                        <section onClick={navigateToWebDevelopment}>
                          <Row>
                            <Col lg={12} md={12} sm={12} className="m-0 p-0">
                              <img
                                src={webDevelopment}
                                className={styles["imageClassStyles"]}
                              />
                            </Col>
                          </Row>
                        </section>
                      </motion.div>
                    </Col>
                  </>
                ) : null}

                {digitalMarketing ? (
                  <>
                    <Col
                      lg={4}
                      md={4}
                      sm={4}
                      className={styles["Box_For_Options"]}
                    >
                      <motion.div
                        whileHover="hover" // Apply the hover animation on hover
                        variants={cardVariants}
                      >
                        <section onClick={handleDigitalMarketing}>
                          <Row>
                            <Col lg={12} md={12} sm={12} className="m-0 p-0">
                              <img
                                src={Digital}
                                className={styles["imageClassStyles"]}
                              />
                            </Col>
                          </Row>
                        </section>
                      </motion.div>
                    </Col>
                  </>
                ) : null}

                {graphicsDesign ? (
                  <>
                    <Col
                      lg={4}
                      md={4}
                      sm={4}
                      className={styles["Box_For_Options"]}
                    >
                      <motion.div
                        whileHover="hover" // Apply the hover animation on hover
                        variants={cardVariants}
                      >
                        <section onClick={handleGraphicsDesign}>
                          <Row>
                            <Col lg={12} md={12} sm={12} className="m-0 p-0">
                              <img
                                src={graphics}
                                className={styles["imageClassStyles"]}
                              />
                            </Col>
                          </Row>
                        </section>
                      </motion.div>
                    </Col>
                  </>
                ) : null}
              </Row>
            </Col>
          </Row>
          <Row>
            <Col lg={12} md={12} sm={12}>
              {webData ? (
                <WebDevelopment
                  setwebData={setwebData}
                  setdigitalMarketing={setdigitalMarketing}
                  setgraphicsDesign={setgraphicsDesign}
                />
              ) : null}
              {graphicsData ? (
                <GraphicsDesign
                  setgraphicsData={setgraphicsData}
                  setwebDevDepart={setwebDevDepart}
                  setdigitalMarketing={setdigitalMarketing}
                />
              ) : null}
              {digitalmarketdata ? (
                <DigitalMarketing
                  setdigitalmarketdata={setdigitalmarketdata}
                  setwebDevDepart={setwebDevDepart}
                  setgraphicsDesign={setgraphicsDesign}
                />
              ) : null}
            </Col>
          </Row>
          <Row className="mt-5">
            <Col lg={12} md={12} sm={12}>
              <section className={styles["BackGroundOurPhilosophy"]}>
                <Row className="mt-3">
                  <Col
                    lg={12}
                    md={12}
                    sm={12}
                    className="d-flex justify-content-center"
                  >
                    <span className={styles["ourPhilosophy"]}>
                      Our Philosophy
                    </span>
                  </Col>
                </Row>
                <Row>
                  <Col lg={12} md={12} sm={12}>
                    <section className={styles["PhilosophyClass"]}>
                      <span>
                        industry. Lorem Ipsum has been the industry's standard
                        dummy text ever since the 1500s, when an unknown printer
                        took a galley of type and scrambled it to make a type
                        specimen book. It has survived not only five centuries,
                        but also the leap into electronic typesetting, remaining
                        essentially unchanged. It was popularised in the 1960s
                        with the release of Letraset sheets containing Lorem
                        Ipsum passages, and more recently with desktop
                        publishing software like Aldus PageMaker including
                        versions of Lorem Ipsum.
                      </span>
                    </section>
                  </Col>
                </Row>
              </section>
            </Col>
          </Row>

          <Row className="mt-5">
            <Col
              lg={12}
              md={12}
              sm={12}
              className="d-flex justify-content-center"
            >
              <span className={styles["More_headings_Services"]}>Our Team</span>
            </Col>
          </Row>
          <Row className="mt-4">
            <Col lg={3} md={3} sm={3}>
              <section className={styles["TeamMembers"]}>
                <Row>
                  <Col
                    lg={12}
                    md={12}
                    sm={12}
                    className="d-flex flex-column align-items-center"
                  >
                    <img
                      src={profile}
                      width={73}
                      className={styles["profilePic"]}
                    />
                    <span>Talha Tariq</span>
                  </Col>
                </Row>
              </section>
            </Col>
            <Col lg={3} md={3} sm={3}>
              <section className={styles["TeamMembers"]}>
                <Row>
                  <Col
                    lg={12}
                    md={12}
                    sm={12}
                    className="d-flex flex-column align-items-center"
                  >
                    <img
                      src={profile}
                      width={73}
                      className={styles["profilePic"]}
                    />
                    <span>Saif ul islam</span>
                  </Col>
                </Row>
              </section>
            </Col>
            <Col lg={3} md={3} sm={3}>
              <section className={styles["TeamMembers"]}>
                <Row>
                  <Col
                    lg={12}
                    md={12}
                    sm={12}
                    className="d-flex flex-column align-items-center"
                  >
                    <img
                      src={profile}
                      width={73}
                      className={styles["profilePic"]}
                    />
                    <span>Hassan Bin Saleem</span>
                  </Col>
                </Row>
              </section>
            </Col>
            <Col lg={3} md={3} sm={3}>
              <section className={styles["TeamMembers"]}>
                <Row>
                  <Col
                    lg={12}
                    md={12}
                    sm={12}
                    className="d-flex flex-column align-items-center"
                  >
                    <img
                      src={profile}
                      width={73}
                      className={styles["profilePic"]}
                    />
                    <span>Muhammad Yousuf</span>
                  </Col>
                </Row>
              </section>
            </Col>
          </Row>
        </section>
      </Col>
    </Row>
  );
};

export default Outlet;
