import React, { useEffect, useState } from "react";
import styles from "./Header.module.css";
import { Col, Row } from "react-bootstrap";
import Codinetslogo from "../../assets/CodinetsLogo.jpeg";
import { motion, useAnimation } from "framer-motion";
import { useNavigate } from "react-router-dom";

const Header = () => {
  const controls = useAnimation();
  const navigate = useNavigate();
  const [shouldAnimate, setShouldAnimate] = useState(false);

  // Function to trigger animation
  const animateHeader = async () => {
    await controls.start({
      scale: [1, 0.8, 1], // Scale from 1 to 0.8 and back to 1
      rotateY: [0, 180, 0], // Flip 180 degrees and back
      transition: { duration: 0.8, ease: "easeInOut" }, // Animation duration and easing
    });
  };

  // Trigger the animation when the component mounts and on every refresh
  useEffect(() => {
    if (shouldAnimate) {
      animateHeader();
    }
  }, [shouldAnimate]);

  // Set shouldAnimate to true when the component mounts or on refresh
  useEffect(() => {
    setShouldAnimate(true);
  }, []);

  const HandleContactUsPage = () => {
    navigate("/contact");
  };

  const handleHomePage = () => {
    navigate("/");
  };

  const handleBaoutPage = () => {
    navigate("/about");
  };

  const handleCarrerPage = () => {
    navigate("/career");
  };

  const handleBeingWithCodinets = () => {
    navigate("/Beingwithcodinets");
  };

  return (
    <Row>
      <Col lg={12} md={12} sm={12}>
        <section className={styles["BackGroundHeader"]}>
          <Row>
            <Col lg={3} md={3} sm={3}>
              <motion.div
                className="d-flex flex-column"
                animate={controls}
                initial={{ scale: 1, rotateY: 0 }}
              >
                <Row className="mt-2">
                  <Col
                    lg={12}
                    md={12}
                    sm={12}
                    className="d-flex justify-content-center"
                  >
                    <div className="d-flex flex-column">
                      <span className={styles["COD_Heading"]}>CODINETS</span>

                      <span className={styles["COD_Sub_Heading"]}>
                        Your Partner For Innovation
                      </span>
                    </div>
                  </Col>
                </Row>
              </motion.div>
            </Col>
            <Col lg={9} md={9} sm={9}>
              <Row className="m-0 p-0 mt-4">
                <Col
                  lg={11}
                  md={11}
                  sm={11}
                  className="d-flex justify-content-end gap-4 "
                >
                  <span className={styles["Options"]} onClick={handleHomePage}>
                    HOME
                  </span>
                  <span className={styles["Options"]} onClick={handleBaoutPage}>
                    ABOUT US
                  </span>
                  <span
                    className={styles["Options"]}
                    onClick={handleBeingWithCodinets}
                  >
                    BLOGS
                  </span>
                  <span
                    className={styles["Options"]}
                    onClick={handleCarrerPage}
                  >
                    CARRER
                  </span>
                  <span
                    className={styles["Options"]}
                    onClick={HandleContactUsPage}
                  >
                    CONTACT US
                  </span>
                </Col>
              </Row>
            </Col>
          </Row>
        </section>
      </Col>
    </Row>
  );
};

export default Header;
